

import Link from 'next/link'
import courseData from '../data/music_course.json'

interface Course {
    id: number
    title: string
    description: string
    price: number
    instructor: string
    isFeatured: boolean
    slug: string

}

function FeatureCourses() {

    const featuredCourses= courseData.courses.filter((course: Course) => course.isFeatured)

  return (
    <div className='px-12 bg-gray-900'>
        <div>
            <div className='text-center'>
                <h1 className='text-lg text-cyan-500 font-semibold tracking-wide uppercase '>Featured Courses</h1>
                <p className='mt-2 text-3xl leading-8 font-semibold tracking-tight text-white md:text-4xl'>Learn with the Best.</p>
            </div>

        </div>
        <div className='mt-10'>
            <div className='grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-3 gap-8 justify-center'>
                {}
            </div>
        </div>

        <div className='mt-20 text-center'>
            <Link href={"/courses"}
            className='inline-block px-3 py-2 text-lg font-semibold  uppercase bg-cyan-500 text-white rounded-md'
            >
                View All Courses
            </Link>
        </div>

    </div>
  )
}

export default FeatureCourses